@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*{
    font-family: 'Roboto', sans-serif;
}

form .form-group p {
    color: red;
    font-size: 14px;
}

.main-header{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 10px 0px 50px 0px;

}

.actions{
    display: flex;
    gap: 10px;
    padding: 20px 0;
}

.nav_button{
    padding: 5px 10px;
    font-size: 20px;
    border: 1px solid black;
    border-radius: 5px;
    background: #4eb6487a;
}

.button{
    padding: 5px 10px;
    font-size: 20px;
    border: 1px solid black;
    border-radius: 5px;
    background: #ee425a;
}

.headline{
    color: #545454;
    font-size: 2em;
    font-weight:700 ;
    padding: 20px 0;
}

p{
    font-size: 18px;
}

.bold{
    font-weight: 700;
}

.area{
    display: flex;
    flex-direction: row;
    gap:10px;
    color: #000;
    font-size: 18px;

}
.living-room{
    width: 600px;
    height: 300px;
    background-color: rgb(0, 60, 255);
    opacity: 0.7;
    justify-content: center;
    align-items: center;
    display: flex;
    border: 1px solid black;
    color: #000;
    padding: 20px;
    gap: 20px;
    flex-direction: column;
}

.kitchen-room{
    width: 300px;
    height: 300px;
    background-color: rgb(0, 60, 255);
    opacity: 0.7;
    justify-content: center;
    align-items: center;
    display: flex;
    border: 1px solid black;
    color: #000;
    padding: 20px;
    gap: 20px;
    flex-direction: column;
}

.logo_symbol{
    width: 50px;
    height: 60px;
}